.product {
  display: flex;
  flex-direction: column;

  padding: 15px 14px;
  width: $product-width;

  border: $border-color 1px solid;
  border-radius: 5px;

  &__picture {
    width: 160px;
    height: 134px;
  }

  &__picture-wrapper {
    display: flex;
    align-self: center;

    margin-bottom: 30px;
    padding: 12px 0;

    border: 3px solid transparent;

    transition: transform linear 0.5s;

    &:hover {
      border-color: $border-color;

      transform: scale(1.2);
    }
  }

  &__heading {
    margin-bottom: 5px;

    font-weight: 500;
    font-size: 12px;
    line-height: 15px;
  }

  &__id {
    margin-bottom: 15px;

    font-weight: 400;
    font-size: $small-font;
  }

  &__price {
    @extend .product__rating;
  }

  &__price-word {
    align-self: flex-end;

    padding-bottom: 1px;

    font-weight: 400;
    font-size: $small-font;
    line-height: 13px;

    text-transform: capitalize;
  }

  &__price-result {
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;
  }

  &__rating {
    display: flex;
    justify-content: space-between;
    align-items: center;

    margin-bottom: 20px;
  }

  &__review-link {
    display: block;

    font-weight: 400;
    font-size: $small-font;
    text-decoration: none;
    color: black;

    text-transform: capitalize;
  }

  &__stars {
    display: flex;
    justify-content: space-between;
  }

  &__star {
    width: 15px;
    height: 15px;
    margin-right: 4px;

    background: url("../images/star.svg") center no-repeat;
  }

  &__button {
    line-height: 100%;
    width: 170px;
    height: 40px;
    border: 1px solid transparent;
    border-radius: 5px;
    background-color: $active-color;
    text-transform: uppercase;
    color: #fff;
    font-weight: 700;
    font-size: 14px;

    transition: 0.2s linear;

    &:hover {
      background-color: white;
      color: $active-color;
      border-color: $active-color;
      cursor: pointer;
    }
  }
}

@for $i from 1 through 5 {
  .stars--#{$i} :nth-child(-n + #{$i}) {
    background-image: url("../images/star-active.svg");
  }
}
